@use 'sass:map';
@use './node_modules/@angular/material' as mat;

@include mat.core();

$dataculture-typography: mat.define-typography-config(
        $display-4: mat.define-typography-level(3.8rem, $font-weight: 600, $font-family: Roboto, $letter-spacing: 0.03em),
        $display-3: mat.define-typography-level(2.8rem, $font-weight: 600, $font-family: Roboto, $letter-spacing: 0.02em),
        $display-2: mat.define-typography-level(2.2rem, $font-weight: 500, $font-family: Roboto, $letter-spacing: 0.02em),
        $display-1: mat.define-typography-level(2.0rem, $font-weight: 500, $font-family: Roboto, $letter-spacing: 0.01em),
        $headline: mat.define-typography-level(1.8rem, $font-weight: 500, $font-family: Roboto, $letter-spacing: 0.03em),       //h1
        $title: mat.define-typography-level(1.5rem, $font-weight: 300, $font-family: Roboto, $letter-spacing: 0.03em),          //h2
        $subheading-2: mat.define-typography-level(16px, 16px, 400, Roboto, $letter-spacing: -0.01em),                          //h3
        $subheading-1: mat.define-typography-level(14px, 14px, 400, Roboto, $letter-spacing: -0.01em),                          //h4
        $body-2: mat.define-typography-level(12px, 12px, 400, Roboto, $letter-spacing: -0.03em),
        $body-1: mat.define-typography-level(11px, 11px, 300, Roboto, $letter-spacing: -0.03em),
        $button: mat.define-typography-level(11px, 11px, 300, Roboto, $letter-spacing: -0.03em),
        $caption: mat.define-typography-level(10px, 10px, 300, Roboto, $letter-spacing: -0.03em),
        $input: mat.define-typography-level(1em, 1em, 300, Roboto, $letter-spacing: -0.03em)
);



//voir https://material.angular.io/guide/theming#palettes
$dataculture-red-palette :(
        50: #feecee,
        100: #fcd0d3,
        200: #ea9f9d,
        300: #df7b78,
        400: #e85f56,
        500: #ec533e,
        600: #de4a3d,
        700: #cc4137,
        800: #bf3b31,
        900: #af3326,
        contrast: (
                50: rgba(black,0.87),
                100: rgba(black,0.87),
                200: rgba(black,0.87),
                300: rgba(black,0.87),
                400: rgba(black,0.87),
                500: white,
                600: white,
                700: white,
                800: white,
                900: white
        )
);

$dataculture-grey-palette :(
        50: #f8f9fc,
        100: #f1f2f5,
        200: #e8e9ec,
        300: #d8d9dc,
        400: #b4b6b8,
        500: #949698,
        600: #6c6d70,
        700: #3a3b3e,
        800: #232429,
        900: #1a1b1d,
        contrast: (
                50: rgba(black,0.87),
                100: rgba(black,0.87),
                200: rgba(black,0.87),
                300: rgba(black,0.87),
                400: rgba(black,0.87),
                500: rgba(black,0.87),
                600: rgba(black,0.87),
                700: white,
                800: white,
                900: white
        )
);


//Dfinition des couleurs principales utilis dans Data Culture

//Background page : #1A1B1D
//Background menu et blocs : #232429
//
//Pictos gris : #72737C
//
//Rouge : #CC4137
//Orange : #E4983C
//Vert : #81C784
//Bleu : #5471D2
//
//Texte blanc : #ffffff
//Texte gris : ##8B8C93



$dataculture-primary-color: mat.define-palette($dataculture-red-palette, 700);
$dataculture-accent-color:mat.define-palette($dataculture-red-palette, 700);
$dataculture-warn-color: mat.define-palette($dataculture-red-palette, 900);
$dataculture-green: #81C784;
$dataculture-blue: #5471D2;
$dataculture-red: #CC4137;
$dataculture-orange: #E4983C;
$dataculture-semigrey: mat.define-palette($dataculture-grey-palette,500);
$dataculture-background: mat.define-palette($dataculture-grey-palette,900);
$dataculture-foreground: mat.define-palette($dataculture-grey-palette,50);


$dataculture-theme:mat.define-dark-theme((
        color: (
                primary: $dataculture-primary-color,
                accent: $dataculture-accent-color,
                warn:  $dataculture-warn-color,
        ),
        typography: $dataculture-typography,
));




@include mat.all-component-themes($dataculture-theme);
@include mat.typography-hierarchy($dataculture-theme);
//@include mat.all-component-typographies($dataculture-typography);


.student-name {
        @include mat.typography-level($dataculture-typography,"body-2");
}

.student-promo {
        @include mat.typography-level($dataculture-typography,"body-1");
}

.student-department {
        @include mat.typography-level($dataculture-typography,"body-1");
}

.expanded-plus {
        @include mat.typography-level($dataculture-typography,"title");
        color: $dataculture-orange
}

.appli-title {
        @include mat.typography-level($dataculture-typography,"subheading-2");
        font-size: calc(13px + 0.8vw);
        font-variant-caps: all-small-caps;
        margin: 8px;
}

.film-title {
        @include mat.typography-level($dataculture-typography,"body-1");
}

.blur {
        filter: blur(0.4px);
}
